*{
    font-family: Helvetica ,Arial, sans-serif;
    margin: 0px;
}

html, body{
    background-color:$backGround;
    padding-bottom: 20px;
}

.boxTest{
    background-color: red;
}

.mr-5{
        margin-left: 15px; 
}

.ml-5{
    margin-left: 15px;
}

////Login 

.backgroundContainer{
    background-color: $backGround;
    display: grid;
    justify-content: center;
}


.containerLoginScreen{
    margin-top: 40%;
    background-color:$backGroundContainer;
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    text-align: center;
    align-content:flex-start;
    width: 500px;
    padding: 20px;
    border-radius: 20px;
}

.imageCenter{
    display: block;
    margin-left: auto;
    margin-right: auto;
    // margin-top: 10px;
    margin-bottom: 20px;
    height: 15vh;
}

.form-group {
    display: grid;
    grid-template-columns: 1fr;
    &.m-1{
        margin-left: 10px;
        margin-right: 10px;
    }
    
}

.form-control {
    margin: 5px;
    height: calc(1.5em + .75rem + 2px);
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;

    &:focus{
        outline: none !important;
        border:1px solid $primary;
        box-shadow: 0 0 10px #719ECE;
    }
}

.button{
    cursor: pointer;
    color: white;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    // justify-self: center;
    background-color: $buttonBase;

    
    &:hover{
        background-color: $backGround;
    }

    &.unactive{
        background-color: transparent;
        color: grey;
        &:hover{
            background-color: transparent;
        }
    }

}

.mainConatainer{
    display: grid;
    margin-top: 5vh;
    padding: 10px;
    justify-content: center;
    align-content: flex-start;
    background-color:$backGroundContainer;
    border-radius: 20px;
    width: 85vw;
    height: 85vh;
    grid-template-columns: 1fr;
    grid-template-rows: 40px 60px 1fr;
}

.headerText{
    font-size: 25px;
    text-align: center;
    font-weight: bold;
    border-bottom: 6px double $backGround;
}

.headerTabs{
    display: grid;
    grid-template-columns: repeat(3,1fr);
}

.infoContainer{
    height: 100%;
    max-height: max-content;
    overflow-y: scroll;
}

.itemContainer{
    display: grid;
    grid-template-columns: 1fr 30px;
    background: rgb(5,70,204);
    background: radial-gradient(circle, rgba(5,70,204,1) 32%, rgba(3,51,129,1) 65%);
    margin: 10px 5px;
    padding: 5px;
    border-radius: 10px;
    color: white;
    cursor: pointer;
    max-height: 40px;

    &.round{
        grid-template-columns: 60px 1fr  40px 1fr;
        column-gap: 20px;
        text-align: center;
    }

    &.player{
        grid-template-columns: 30px 1fr 50px;
    }

    &.fastest{
        background: rgb(2,131,19);
        background: radial-gradient(circle, rgba(2,131,19,1) 32%, rgba(6,116,107,1) 65%);
    }
}

.separatorContainer{
    background-color: white;
    color: black;
    align-self: center;
    border-radius: 20px;
    padding: 5px;

    &.small{
        padding: 1px; 
        text-align: center;
    }

}

.roundSummaryContainer{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 100px 52vh;
}

.RoundSumaryHeader{
    display: grid;
    justify-content: center;
    align-content: flex-end;
}

.roundNameContainer{
    width: 70vw;
    min-height: 30px;
    border-radius: 60px;
    padding: 10px;
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    background: rgb(186,186,183);
    background: radial-gradient(circle, rgba(186,186,183,1) 32%, rgba(255,247,69,1) 65%);
    display: grid;
    grid-template-columns: 20px 1fr 40px;
}

.roundSummaryInfo{
    height: 70vh;
    display: grid;
    grid-template-columns: 1fr 10vw 1fr;

}

.iconButton{
    cursor: pointer;
}


@mixin roundTeamContainer{
    display: grid;
    grid-template-rows: repeat(7,1fr);
    font-size: 18px
}

.roundSummaryTeamA{
    @include roundTeamContainer;
}

.roundSummaryTeamB{
    @include roundTeamContainer;
}

.headerTeamName{
    justify-self: center;
    align-self: center;
    padding: 10px 70px;
    border-radius: 20px;
    background-color: $backGroundContainer;

    &.fastest{
        background: rgb(2,131,19);
        background: radial-gradient(circle, rgba(2,131,19,1) 32%, rgba(6,116,107,1) 65%);
    }
}

.pageRounSummary_tabsContainer{
    background-color: $backGroundContainer;
    position: fixed;
    width: 100%;
    bottom: 0;
    min-height: 20px;
    display: grid;
    grid-auto-flow: column;
    justify-content: space-around;
    padding: 10px 0px;
}

.pageTabs{
    color:grey;
    cursor: pointer;
    &.active{
        color: white;
    }
}

.roundSummaryArrowContainer{
    display: grid;
    grid-template-columns: 1fr 70px 1fr;
}

.arrow{
    color: green;
    font-weight: bold;
    cursor: pointer;
    
    &.left{
        justify-self: right;
    }

    &.right{
        justify-self: left;
    }
}

.activeQuestion{
    justify-self: center;
    align-self: center;
    font-size: 25px;
    color: white;
    font-weight: bold;
}

.logoFlotante{
    position: fixed;
    height: 12vh;
    right: 5px;

}

.preguntaContainer{
    display: grid;
    justify-content: center;
    align-content: center;

}

.preguntaInternalContainer{
    background-color: $backGroundContainer;
    width: 80vw;
    height: 60px;
    border-radius: 10px;
    padding: 5px;
}