$height:8px;

.loadingExternalContainer{
    position: relative;
}

.loadingContainer{

    display: grid;
    grid-template-columns: minmax(1fr,1fr);
    grid-auto-flow: column dense;
    height: $height;
    background-color: lighten($color: $waitingColor, $amount: 30);
    border-radius: 30px;
}

.activeContainer {
    height: $height;
    background-color: $waitingColor;
    border-radius: 30px;
}

.waitingText{
    position: absolute;
    top: -5px;
    left: 40%;
    color: $activeText;
}






